"use client";

import { useState, useEffect } from "react";
import axios from "axios";
import io from "socket.io-client";
import { motion } from "framer-motion";

export default function Home() {
  const [command, setCommand] = useState("");
  const [userId] = useState("default-user"); // Replace with auth logic
  const [generatedHtml, setGeneratedHtml] = useState("");
  const [templateId, setTemplateId] = useState("");
  const [socket, setSocket] = useState<any>(null);

  useEffect(() => {
    function initializeSocket() {
      const newSocket = io(
        process.env.NEXT_PUBLIC_API_URL || "http://127.0.0.1:5000"
      );
      setSocket(newSocket);

      newSocket.on(
        "ui-update",
        (data: { html: string; template_id: string }) => {
          setGeneratedHtml(data.html);
          setTemplateId(data.template_id);
        }
      );

      return () => newSocket.close();
    }
    initializeSocket();
  }, []);

  const generateUI = async () => {
    try {
      const res = await axios.post(
        `${process.env.NEXT_PUBLIC_API_URL}/generate-ui`,
        {
          command,
          user_id: userId,
        }
      );
      setGeneratedHtml(res.data.html);
      setTemplateId(res.data.template_id);
    } catch (error) {
      console.error("Error generating UI:", error);
    }
  };

  const saveUI = async (feedback: boolean) => {
    try {
      await axios.post(`${process.env.NEXT_PUBLIC_API_URL}/save-ui`, {
        html: generatedHtml,
        parent_template_id: templateId,
        user: userId,
        feedback,
      });
      alert("UI saved successfully!");
    } catch (error) {
      console.error("Error saving UI:", error);
    }
  };

  const updateUI = () => {
    if (socket) socket.emit("update-command", { command }); // Trigger backend update
  };

  return (
    <div className="min-h-screen bg-ai-gradient text-white flex flex-col items-center justify-center p-8">
      <motion.h1
        initial={{ opacity: 0 }}
        animate={{ opacity: 1 }}
        className="text-4xl font-bold mb-8"
      >
        Agentic AI UI Generator
      </motion.h1>

      <form
        onSubmit={(e) => {
          e.preventDefault();
          generateUI();
        }}
        className="w-full max-w-lg"
      >
        <input
          type="text"
          value={command}
          onChange={(e) => setCommand(e.target.value)}
          placeholder="e.g., Generate a responsive contact form"
          className="w-full p-4 rounded-lg bg-ai-primary text-white border border-ai-accent mb-4"
        />
        <button
          type="submit"
          className="bg-ai-accent text-white p-4 rounded-lg w-full"
        >
          Generate UI
        </button>
      </form>

      <motion.div
        initial={{ scale: 0.9 }}
        animate={{ scale: 1 }}
        className="mt-8 w-full max-w-4xl bg-ai-primary p-6 rounded-lg shadow-lg"
      >
        <h2 className="text-2xl mb-4">Live Preview</h2>
        <div dangerouslySetInnerHTML={{ __html: generatedHtml }} />
      </motion.div>

      {generatedHtml && (
        <div className="mt-4 flex space-x-4">
          <button
            onClick={updateUI}
            className="bg-ai-accent text-white p-2 rounded"
          >
            Update UI
          </button>
          <button
            onClick={() => saveUI(true)}
            className="bg-green-500 text-white p-2 rounded"
          >
            Save with Positive Feedback
          </button>
        </div>
      )}
    </div>
  );
}
